.container {
  .burgerPages {

    .openedBurgerPage {
      width: 100vw;
      height: 50px;
      background-color: #638892;
      color: white;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font-size: 24px;
      //position: relative;
      z-index: 1200;
      padding-left: 20px;
    }

    .burgerMenuButton {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      display: inline;
      font-size: 24px;
      border: 0;
      border-radius: 5px;
      background-color: white;
      opacity: 0;
      z-index: 99;
      padding: 0 40px 0 0 ;

    }



    .burgerMenu {
      height: 100vh;
      width: 100%;
      background-color: #638892;
      position: absolute;
      top: -100vh;
      left: 0;
      transition: top 0.2s linear;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 40px;
      color: white;

      .burgerButtons {
        margin-top: 15vh;
        width: 80%;

        .burgerButton {
          color: white;
          cursor: default;
        }

        .line {
          display: block;
          width: 100%;
          height: 3px;
          background-color: white;
        }
      }
    }

    .active {
      top: 0;
    }
  }
}

//максимальный размер header
@media (max-width: 820px) {
  .container {
    .burgerPages {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 101;

    }
  }

}

@media (max-height: 430px) {
  .container {

    .burgerPages {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 101;
      .burgerMenu {

        font-size: 35px;
        .burgerButtons {

          margin-top: 5vh;
        }
      }
    }
  }

}
