.container {
  position: fixed;
  top: 50%;
  left: 50%;

  z-index: 1000;

  .overlay {
    position: fixed;
    height: 100vh;
    width: 100vw;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: black;
    opacity: 0.6;


  }

  .window {
    position: absolute;
    transform: translate(-50%, -44%);
    padding: 25px;
    background-color: #F4F6F7;
    height: 554px;
    width: 550px;
    border-radius: 14px;

    .backButton{
      position: absolute;
      left: 25px;
      bottom: 75px;
      width: 100px;
      height: 30px;
      border: 1px solid #D9D9D9;
      background-color: #FFFFFF;
      border-radius: 7px;
      //display: flex;
      align-items: center;
      justify-content: center;
      cursor: default;
      display: none;


      &:active {
        transform: translateY(0.5px);
      }
    }

  }

}

@media (width<=820px) {
  .container {
    left: 0;
    top: 50px;

    .overlay{
      display: none;
    }
    .window {
      display: flex;
      flex-direction: column;
      position: static;
      border-radius: 0;

      height: calc(100svh - 50px);
      width: 100vw;
      left: 0;
      top: 0;
      transform: translate(0, 0);
      .backButton{
        display: flex;
      }

    }
  }
}

