.container {
  .editButton {
    position: absolute;
    right: 37px;
    bottom: 15px;
    width: 80px;
    background-color: white;
    border: 1px #D9D9D9 solid;
    border-radius: 7px;
    cursor: pointer;

    &:active {
      transform: translateY(1px);
    }
  }

  .cancelButton {
    position: absolute;
    left: 39px;
    bottom: 15px;
    width: 80px;
    background-color: white;
    border: 1px #D9D9D9 solid;
    border-radius: 7px;
    cursor: pointer;

    &:active {
      transform: translateY(1px);
    }
  }
}

@media (max-width: 820px) and (min-height: 430px) {
  .container {
    .editButton {
      cursor: default;
      right: 20px;
      bottom: 20px;
    }

    .cancelButton {
      cursor: default;
      left: 20px;
      bottom: 20px;
    }
  }
}

@media (max-height: 430px) {
  .container {
    .editButton {
      bottom: 4px;
      right: 30px;
    }
  }
}